function toFixed(value) {
    if (Math.abs(value) < 1.0) {
        var exp = parseInt(value.toString().split('-')[1]);
        if (exp) {
            return value.toFixed(exp);
        }
    }
    return value;
}
/**
 * Tag class
 */
class Tag {
    constructor(groupCode, value = '', version = 'AC1009', nullable = true) {
        this._nullable = nullable;
        this._version = version;
        this._groupCode = groupCode;
        this._value = value;
    }
    get groupCode() { return this._groupCode; }
    get nullable() { return this._nullable; }
    get value() { return this._value; }
    get version() { return this._version; }
    set groupCode(value) { this._groupCode = value; }
    set nullable(value) { this._nullable = value; }
    set value(value) { this._value = value; }
    set version(value) { this._version = value; }
    stringify() {
        if (this.nullable || this.value !== '') {
            if (typeof (this._value) === 'number') {
                return `  ${this.groupCode}\n${toFixed(this._value)}\n`;
            }
            // Do not remove the tow spaces in the string 👍 I like them
            return `  ${this.groupCode}\n${this.value}\n`;
        }
        return '';
    }
}

class NameComponent {
    constructor(name, groupCode = 2) {
        this._tag = new Tag(groupCode, name);
    }
    get groupCode() { return this._tag.groupCode; }
    get name() { return this._tag.value.toString(); }
    set groupCode(value) { this._tag.groupCode = value; }
    set name(value) { this._tag.value = value; }
    stringify() {
        return this._tag.stringify();
    }
    tags() {
        return [this._tag];
    }
}

