class PointComponent {
    constructor(x, y, z = 0, is3D = false, digit = 0) {
        this._digit = 0;
        this._x = new Tag(parseInt(`1${digit}`), x);
        this._y = new Tag(parseInt(`2${digit}`), y);
        this._z = new Tag(parseInt(`3${digit}`), z);
        this._is3D = is3D;
        this._digit = digit;
    }
    get x() { return parseInt(this._x.value.toString()); }
    get z() { return parseInt(this._z.value.toString()); }
    get y() { return parseInt(this._y.value.toString()); }
    get digit() { return this._digit; }
    set y(value) { this._y.value = value; }
    set z(value) { this._z.value = value; }
    set x(value) { this._x.value = value; }
    set digit(value) {
        if (value <= 8 && value >= 0) {
            this._digit = value;
            this._x.groupCode = parseInt(`1${this.digit}`);
            this._y.groupCode = parseInt(`2${this.digit}`);
            this._z.groupCode = parseInt(`3${this.digit}`);
        }
    }
    stringify() {
        let str = '';
        str += this._x.stringify();
        str += this._y.stringify();
        if (this._is3D)
            str += this._z.stringify();
        return str;
    }
    tags() {
        if (this._is3D)
            return [this._x, this._y, this._z];
        return [this._x, this._y];
    }
}

