class TrueColorComponent {
    constructor(red, green, blue, digit = 0) {
        this._red = red;
        this._green = green;
        this._blue = blue;
        this._digit = digit;
        this._color = new Tag(parseInt(`42${digit}`), this.toDecimal());
    }
    get digit() {
        return this._digit;
    }
    set digit(value) {
        this._digit = value;
        this._color.groupCode = parseInt(`42${this.digit}`);
    }
    get red() {
        return this._red;
    }
    set red(value) {
        this._red = value;
        this._color.value = this.toDecimal();
    }
    get green() {
        return this._green;
    }
    set green(value) {
        this._green = value;
        this._color.value = this.toDecimal();
    }
    get blue() {
        return this._blue;
    }
    set blue(value) {
        this._blue = value;
    }
    toDecimal() {
        const red = String(this.red.toString(16).toUpperCase());
        const green = String(this.green.toString(16).toUpperCase());
        const blue = String(this.blue.toString(16).toUpperCase());
        return parseInt(`${red}${green}${blue}`, 16);
    }
    stringify() {
        return this._color.stringify();
    }
    tags() {
        return [this._color];
    }
}

