class Variable extends DXFManager {
    constructor(variableName, values) {
        super();
        this._values = values;
        this._variableName = variableName;
    }
    get values() {
        return this._values;
    }
    get variableName() {
        return this._variableName;
    }
    tags() {
        return this.makeStandard([[9, `$${this.variableName}`], ...this.values]);
    }
}

class Entity extends DXFManager {
    /**
     * Entity class is the base class of all enities.
     *
     * @link http://help.autodesk.com/view/OARX/2018/ENU/?guid=GUID-7D07C886-FD1D-4A0C-A7AB-B4D21F18E484.
     *
     * @param type {string} The type of the entity example : LINE, POLYLINE, ARC, CIRCLE ....
     * @param subclass
     */
    constructor(type, subclass_marker) {
        super();
        this._type = type;
        this._subclass_marker = subclass_marker;
        this._layer_name = DXFManager.currentLayer;
    }
    get layerName() { return this._layer_name; }
    get subclassMarker() { return this._subclass_marker; }
    get type() { return this._type; }
    /**
     * get the boundingBox of an entity
     * @returns {number[][]}
     */
    boundingBox() {
        return [];
    }
    /**
     * get the array tags of the entity.
     *
     * @returns {Tag[]} Array of Tag.
     */
    tags() {
        return [
            ...this.makeEntityType(this._type),
            ...this.makeHandle(this.handle),
            ...this.makeSubclassMarker('AcDbEntity'),
            ...this.makeLayer(this._layer_name),
            ...this.makeSubclassMarker(this._subclass_marker)
        ];
    }
}

class Arc extends Entity {
    constructor(center, radius, startAngle, endAngle) {
        super('ARC', 'AcDbCircle');
        this._center = center;
        this._radius = radius;
        this._startAngle = startAngle;
        this._endAngle = endAngle;
    }
    get endAngle() { return this._endAngle; }
    get startAngle() { return this._startAngle; }
    get radius() { return this._radius; }
    get center() { return this._center; }
    boundingBox() {
        return [
            [this.center.x - this.radius, this.center.y + this.radius],
            [this.center.x + this.radius, this.center.y - this.radius]
        ];
    }
    tags() {
        return [
            ...super.tags(),
            ...this.makePoint(this.center.x, this.center.y, this.center.z, true),
            ...this.makeStandard([[40, this.radius]]),
            ...this.makeSubclassMarker('AcDbArc'),
            ...this.makeStandard([[50, this.startAngle], [51, this.endAngle]])
        ];
    }
}

class Line extends Entity {
    constructor(start, end) {
        super('LINE', 'AcDbLine');
        this._start = start;
        this._end = end;
    }
    get start() { return this._start; }
    get end() { return this._end; }
    boundingBox() {
        const xs = [];
        if (this.start.x < this.end.x) {
            xs.push(this.start.x, this.end.x);
        }
        else {
            xs.push(this.end.x, this.start.x);
        }
        const ys = [];
        if (this.start.y < this.end.y) {
            ys.push(this.start.y, this.end.y);
        }
        else {
            ys.push(this.end.y, this.start.y);
        }
        const [minX, maxX] = xs;
        const [minY, maxY] = ys;
        return [
            [minX, maxY],
            [maxX, minY]
        ];
    }
    tags() {
        return [
            ...super.tags(),
            ...this.makePoint(this.start.x, this.start.y, this.start.z, true),
            ...this.makePoint(this.end.x, this.end.y, this.end.z, true, 1)
        ];
    }
}

class Face extends Entity {
    constructor(first, second, third, fourth) {
        super('3DFACE', 'AcDbFace');
        this._first = first;
        this._second = second;
        this._third = third;
        this._fourth = fourth;
    }
    get fourth() { return this._fourth; }
    get third() { return this._third; }
    get second() { return this._second; }
    get first() { return this._first; }
    boundingBox() {
        const arrayX = [this.first.x, this.second.x, this.third.x, this.fourth.x];
        const arrayY = [this.first.y, this.second.y, this.third.y, this.fourth.y];
        const minX = Math.min(...arrayX);
        const maxX = Math.max(...arrayX);
        const minY = Math.min(...arrayY);
        const maxY = Math.max(...arrayY);
        return [
            [minX, maxY],
            [maxX, minY]
        ];
    }
    tags() {
        return [
            ...super.tags(),
            ...this.makePoint(this.first.x, this.first.y, this.first.z, true),
            ...this.makePoint(this.second.x, this.second.y, this.second.z, true, 1),
            ...this.makePoint(this.third.x, this.third.y, this.third.z, true, 2),
            ...this.makePoint(this.fourth.x, this.fourth.y, this.fourth.z, true, 3),
        ];
    }
}

