class Circle extends Entity {
    constructor(center, radius) {
        super('CIRCLE', 'AcDbCircle');
        this._center = center;
        this._radius = radius;
    }
    get radius() { return this._radius; }
    get center() { return this._center; }
    boundingBox() {
        return [
            [this.center.x - this.radius, this.center.y + this.radius],
            [this.center.x + this.radius, this.center.y - this.radius]
        ];
    }
    tags() {
        let tags = super.tags();
        tags.push(...this.makePoint(this.center.x, this.center.y, this.center.z, true));
        tags.push(...this.makeStandard([[40, this.radius]]));
        return tags;
    }
}

class Spline extends Entity {
    constructor(control_points, fit_points = [], curve_degree = 3, flag = 8 /* Planar */, knots = [], weights = []) {
        super('SPLINE', 'AcDbSpline');
        this._control_points = control_points;
        this._curve_degree = curve_degree;
        this._flag = flag;
        this._knots = knots;
        this._weights = weights;
        this._fit_points = fit_points;
        const knotsLength = this._curve_degree + this._control_points.length + 1;
        if (this._knots.length === 0) {
            for (let i = 0; i < this._curve_degree + 1; i++) {
                this._knots.push(0);
            }
            for (let i = 1; i < this._control_points.length - this._curve_degree; i++) {
                this._knots.push(i);
            }
            for (let i = 0; i < this._curve_degree + 1; i++) {
                this._knots.push(this._control_points.length - this._curve_degree);
            }
        }
        if (this._knots.length !== knotsLength) {
            throw new Error(`Invalid knot vector length. Expected ${knotsLength} but received ${this._knots.length}.`);
        }
    }
    get fitPoints() { return this._fit_points; }
    get weights() { return this._weights; }
    get knots() { return this._knots; }
    get flag() { return this._flag; }
    get curveDegree() { return this._curve_degree; }
    get controlPoints() { return this._control_points; }
    boundingBox() {
        const arrayX = [];
        const arrayY = [];
        this._control_points.forEach((control_point) => {
            const [x, y] = control_point;
            arrayX.push(x);
            arrayY.push(y);
        });
        const minX = Math.min(...arrayX);
        const maxX = Math.max(...arrayX);
        const minY = Math.min(...arrayY);
        const maxY = Math.max(...arrayY);
        return [
            [minX, maxY],
            [maxX, minY]
        ];
    }
    tags() {
        const tags = super.tags();
        tags.push(...this.makeStandard([
            [70, this._flag],
            [71, this._curve_degree],
            [72, this._knots.length],
            [73, this._control_points.length],
            [74, this._fit_points.length],
            [42, 0.0000001],
            [43, 0.0000001],
            [42, 0.0000000001],
        ]));
        this._knots.forEach((knot) => {
            tags.push(...this.makeStandard([[40, knot]]));
        });
        this._control_points.forEach((point) => {
            const [x, y, z] = point;
            tags.push(...this.makePoint(x, y, z, true));
        });
        this._fit_points.forEach((fit_point) => {
            const [x, y, z] = fit_point;
            tags.push(...this.makePoint(x, y, z, true, 1));
        });
        return tags;
    }
}

class Ellipse extends Entity {
    constructor(center, x_major_axis, y_major_axis, ratio_minor_axis, start_parameter, end_parameter) {
        super('ELLIPSE', 'AcDbEllipse');
        this._center = center;
        this._x_major_axis = x_major_axis;
        this._y_major_axis = y_major_axis;
        this._ratio_minor_axis = ratio_minor_axis;
        this._start_parameter = start_parameter;
        this._end_parameter = end_parameter;
    }
    get end_parameter() { return this._end_parameter; }
    get start_parameter() { return this._start_parameter; }
    get ratio_minor_axis() { return this._ratio_minor_axis; }
    get y_major_axis() { return this._y_major_axis; }
    get x_major_axis() { return this._x_major_axis; }
    get center() { return this._center; }
    boundingBox() {
        // This is not the correct Bounding Box 😭
        const x = this.center.x;
        const y = this.center.y;
        const xMajor = this.x_major_axis;
        const yMajor = this.y_major_axis;
        const bigRadius = Math.sqrt(Math.pow((x - (x + xMajor)), 2) + Math.pow((y - (y + yMajor)), 2));
        return [
            [this.center.x - bigRadius, this.center.y + bigRadius],
            [this.center.x + bigRadius, this.center.y - bigRadius]
        ];
    }
    tags() {
        return [
            ...super.tags(),
            ...this.makePoint(this.center.x, this.center.y, this.center.z, true),
            ...this.makePoint(this.x_major_axis, this.y_major_axis, 0, false, 1),
            ...this.makeStandard([
                [40, this.ratio_minor_axis],
                [41, this.start_parameter],
                [42, this.end_parameter]
            ])
        ];
    }
}

class Polyline extends Entity {
    constructor(points, flag) {
        super('LWPOLYLINE', 'AcDbPolyline');
        this._points = points;
        this._flag = flag;
    }
    get flag() { return this._flag; }
    get points() { return this._points; }
    boundingBox() {
        const arrayX = [];
        const arrayY = [];
        this.points.forEach((point) => {
            const [x, y] = point;
            arrayX.push(x);
            arrayY.push(y);
        });
        const minX = Math.min(...arrayX);
        const maxX = Math.max(...arrayX);
        const minY = Math.min(...arrayY);
        const maxY = Math.max(...arrayY);
        return [
            [minX, maxY],
            [maxX, minY]
        ];
    }
    tags() {
        let tags = super.tags();
        tags.push(...this.makeStandard([[90, this.points.length]]));
        tags.push(...this.makeStandard([[70, this.flag]]));
        this.points.forEach((point) => {
            const [x, y] = point;
            tags.push(...this.makePoint(x, y));
        });
        return tags;
    }
}

