import * as readline from 'readline'
import * as fs from 'fs'
import DxfParser from 'dxf-parser'
import {Line, RodDescription} from '../../types'


type LayersType = { string: Layer } | {};
export type LinesByLayer = { [Key: string]: Line[] }

class Layer {
    name: string
    height: number

    constructor(name, height) {
        this.height = height
        this.name = name
    }
}


class DxfReader {
    LayersType: LayersType = {}
    dxf: DxfParser
    addButton: HTMLButtonElement
    LayerListElement: HTMLElement
    nameAdd: HTMLInputElement
    heightAdd: HTMLInputElement

    constructor() {
        this.addButton = <HTMLButtonElement>document.getElementById('AddLayer')
        this.addButton.addEventListener('click', this.listenAddButton.bind(this))
        this.nameAdd = <HTMLInputElement>document.getElementById('layerName')
        this.heightAdd = <HTMLInputElement>document.getElementById('layerHeight')
        this.LayerListElement = <HTMLElement>document.getElementById('LayerList')
    }

    GetDxfFile(file: string) {
        this.ClearLayers()
        const  parser: DxfParser = new DxfParser()
        this.dxf = parser.parseSync(file)
        const layers = this.dxf.tables.layer.layers
        for (const layerName in layers) {
            this.AddLayer(layerName, 16)
        }
    }

    listenAddButton() {
        let name: string = this.nameAdd.value
        let height: number = Number(this.heightAdd.value)
        this.AddLayer(name, height)
    }


    Line(filePath: string) {
        let rd = readline.createInterface(fs.createReadStream(filePath), process.stdout)
        rd.on('line', function (line) {
            console.log(line)
        })
    }

    ClearLayers(): void {
        this.LayerListElement.innerHTML = ''
    }

    GetRods(): RodDescription[] {
        let rods: RodDescription[] = []
        for (const entity of this.dxf.entities) {
            if ((entity['layer'] in this.LayersType) && (entity['type'] === 'CIRCLE')) {
                let ds: RodDescription = {
                    x: 0,
                    y: 0,
                    h: 0
                }
                ds.x = parseFloat(entity['center'].x)
                ds.y = parseFloat(entity['center'].y)
                ds.h = this.LayersType[entity['layer']].height
                rods.push(
                    ds
                )
            }
        }
        return rods
    }

    getLines(): LinesByLayer {

        let linesByLayer: LinesByLayer = {}
        for (const entity of this.dxf.entities) {
            if ((entity['type'] === 'LINE')) {
                let ds: Line = {
                    point1: {
                        x: 0,
                        y: 0
                    },
                    point2: {
                        x: 0,
                        y: 0
                    }
                }
                ds.point1.x = parseFloat(entity['vertices'][0].x)
                ds.point2.y = parseFloat(entity['vertices'][0].y)
                ds.point2.x = parseFloat(entity['vertices'][1].x)
                ds.point2.y = parseFloat(entity['vertices'][1].y)
                if (linesByLayer[entity['layer']] === undefined) {
                    linesByLayer[entity['layer']] = []
                }
                linesByLayer[entity['layer']].push(ds)
            }
        }
        return linesByLayer
    }

    AddLayer(name: string, height: number) {
        let layer = new Layer(name, height)
        let liLayer = <HTMLElement>document.createElement('li')
        liLayer.id = `${layer.name}`

        let nameInput = <HTMLInputElement>document.createElement('input')
        nameInput.type = 'text'
        nameInput.value = name

        let heightInput = <HTMLInputElement>document.createElement('input')
        heightInput.type = 'number'
        heightInput.value = height.toString()


        let deleteButton = <HTMLButtonElement>document.createElement('button')
        deleteButton.className = 'btn btn-danger'
        deleteButton.innerHTML = 'Delete'

        liLayer.appendChild(nameInput)
        liLayer.appendChild(heightInput)
        liLayer.appendChild(deleteButton)

        this.LayerListElement.appendChild(liLayer)

        this.LayersType[layer.name] = layer


        nameInput.addEventListener('change', () => {
            this.LayersType[layer.name].name = nameInput.value
        })

        heightInput.addEventListener('change', () => {
            this.LayersType[layer.name].height = heightInput.value

        })

        deleteButton.addEventListener('click', () => {
            liLayer.remove()
            delete this.LayersType[layer.name]
        })
    }

}

export default DxfReader
