// eslint-disable-next-line no-unused-vars
type Formule = (h: number) => number;

class RodParametrs {
    h0Koef: Formule
    r0: Formule
    Lmax: Formule
    Lc: Formule

    constructor(h0Koef: Formule, r0: Formule) {
        this.h0Koef = h0Koef
        this.r0 = r0
        this.Lmax = (h: number): number => {
            return 5 * h
        }
        this.Lc = (h: number): number => {
            return 2.5 * h
        }
    }
}

function ReliabilityOfLightningRod(
    reliability: string,
    h: number
): RodParametrs {
    switch (reliability) {
        case '0,9':
            switch (true) {
                case h > 100 || h <= 150:
                    return new RodParametrs(
                        (h: number): number => {
                            return 0.85 * h
                        },
                        (h: number): number => {
                            return (1.2 - 10 ** -3 * (h - 100)) * h
                        }
                    )
                case h <= 100:
                    return new RodParametrs(
                        (h: number): number => {
                            return 0.85 * h
                        },
                        (h: number): number => {
                            return 1.2 * h
                        }
                    )
                default:
                    return new RodParametrs(
                        (h: number): number => {
                            return 0.85 * h
                        },
                        (h: number): number => {
                            return 1.2 * h
                        }
                    )
            }
        case '0,99':
            switch (true) {
                case h > 100 || h <= 150:
                    return new RodParametrs(
                        (h: number): number => {
                            return (0.8 - 10 ** -3 * (h - 100)) * h
                        },
                        (h: number): number => {
                            return 0.7 * h
                        }
                    )

                case h > 30 || h <= 100:
                    return new RodParametrs(
                        (h: number): number => {
                            return 0.8 * h
                        },
                        (h: number): number => {
                            return (0.8 - 1.43 * 10 ** -3 * (h - 30)) * h
                        }
                    )

                case h <= 30:
                    return new RodParametrs(
                        (h: number): number => {
                            return 0.8 * h
                        },
                        (h: number): number => {
                            return 0.8 * h
                        }
                    )

                default:
                    return new RodParametrs(
                        (h: number): number => {
                            return (0.8 - 10 ** -3 * (h - 100)) * h
                        },
                        (h: number): number => {
                            return 0.8 * h
                        }
                    )
            }

        case '0,999':
            switch (true) {
                case h > 100 || h <= 150:
                    return new RodParametrs(
                        (h: number): number => {
                            return (0.65 - 10 ** -3 * (h - 100)) * h
                        },
                        (h: number): number => {
                            return 0.7 * h
                        }
                    )

                case h > 30 || h <= 100:
                    return new RodParametrs(
                        (h: number): number => {
                            return (0.7 - 7.14 * 10 ** -4 * (h - 30)) * h
                        },
                        (h: number): number => {
                            return (0.6 - 1.43 * 10 ** -3 * (h - 30)) * h
                        }
                    )

                case h <= 30:
                    return new RodParametrs(
                        (h: number): number => {
                            return 0.7 * h
                        },
                        (h: number): number => {
                            return 0.6 * h
                        }
                    )

                default:
                    return new RodParametrs(
                        (h: number): number => {
                            return (0.65 - 10 ** -3 * (h - 100)) * h
                        },
                        (h: number): number => {
                            return 0.7 * h
                        }
                    )
            }
    }
}

export {ReliabilityOfLightningRod, RodParametrs, Formule}
