import {v4 as uuidv4} from 'uuid'
import {Formule, ReliabilityOfLightningRod, RodParametrs} from '../constants/SingleLightningRod'
import {Point} from '../../types'

class Rod {
    uuid: string
    h0: number // height of defend conus
    r0: number //radis of defend conus
    point: Point
    h: number // heigh of rod
    Lmax: number // max distance of interactions 2 rods
    private OtherInteractingRods: Array<string> = [] //list of Interacting

    constructor(x: number, y: number, h: number = 10, reliability: string) {
        this.point = {x, y}
        this.h = h
        this.uuid = uuidv4()

        // console.log(reliability)

        let rodParameter: RodParametrs = ReliabilityOfLightningRod(reliability, h)

        this.h0 = rodParameter.h0Koef(h)
        this.r0 = rodParameter.r0(h)
        this.Lmax = rodParameter.Lmax(h)
    }

    ReCountWithNewReliability(reliability: string) {
        let rodParameter: RodParametrs = ReliabilityOfLightningRod(
            reliability,
            this.h
        )
        this.h0 = rodParameter.h0Koef(this.h)
        this.r0 = rodParameter.r0(this.h)
        this.Lmax = rodParameter.Lmax(this.h)
    }

    GetRadius: Formule = (hx: number): number => {
        if (hx > this.h0) {
            return 0
        }
        return (this.r0 * (this.h0 - hx)) / this.h0
    }

    ClearInteractingRod(): void {
        this.OtherInteractingRods = []
    }

    AddInteractingRod(rod: Rod) {
        if (rod.uuid !== this.uuid) this.OtherInteractingRods.push(rod.uuid)
    }

    GetInteractingRodsList(): Array<string> {
        return this.OtherInteractingRods
    }

    InteractingWithOtherRod() {
        return !(this.OtherInteractingRods.length === 0)
    }

    GetHc(distance: number): number {
        return ((this.Lmax - distance) * this.h0) / (0.5 * this.Lmax)
    }
}

export default Rod
