import {Line, Point} from '../../types'

function RotationDirection(p1x, p1y, p2x, p2y, p3x, p3y) {
    if (((p3y - p1y) * (p2x - p1x)) > ((p2y - p1y) * (p3x - p1x)))
        return 1
    else if (((p3y - p1y) * (p2x - p1x)) == ((p2y - p1y) * (p3x - p1x)))
        return 0

    return -1
}

function containsSegment(x1, y1, x2, y2, sx, sy) {
    if (x1 < x2 && x1 < sx && sx < x2) return true
    else if (x2 < x1 && x2 < sx && sx < x1) return true
    else if (y1 < y2 && y1 < sy && sy < y2) return true
    else if (y2 < y1 && y2 < sy && sy < y1) return true
    else if (x1 == sx && y1 == sy || x2 == sx && y2 == sy) return true
    return false
}


const Utils = {
    GetDistanceBetween2Point: (point1: Point, point2: Point): number => {
        return Math.sqrt((point1.x - point2.x) ** 2 + (point1.y - point2.y) ** 2)
    },

    GetAngleBetween2points: (
        point1: Point,
        point2: Point
    ): number => {
        const deltaY = point2.y - point1.y
        const deltaX = point2.x - point1.x
        const angle = Math.atan(deltaY / deltaX)
        if (deltaX < 0) return angle - Math.PI

        return angle
    },

    GetTangentsCircleByPointOld: (pointC: Point, pointP: Point, r: number): {
        x1: number,
        y1: number,
        x2: number,
        y2: number,
        fi: number
    } => {
        const L: number = Utils.GetDistanceBetween2Point(pointC, pointP) //distance between point and center of circle
        const alpha: number = Utils.GetAngleBetween2points(pointC, pointP)
        const lAspic: number = Math.sqrt(L ** 2 - r ** 2)
        const fi: number = Math.acos(r / lAspic)


        const point1 = Utils.GetNewPointComplex(pointP, lAspic, -alpha + fi + Math.PI)
        const point2 = Utils.GetNewPointComplex(pointP, lAspic, -alpha - fi)

        // console.log('fi', ((-alpha + fi + Math.PI) * 180) / Math.PI)
        // console.log('alpha', ((-alpha - fi + 2 * Math.PI) * 180) / Math.PI)
        const chord: number = Utils.GetDistanceBetween2Point(point1, point2)
        const fiO: number = Math.atan((chord / 2) / r)

        return {
            x1: point1.x,
            y1: point1.y,
            x2: point2.x,
            y2: point2.y,
            fi: fiO
        }
    },

    GetTangentsCircleByPoint:
        (
            pointC: Point,
            radius: number,
            pointP: Point
        ): {
            point1: Point,
            point2: Point,
            fi: number
        } => {

            const dx = pointC.x - pointP.x
            const dy = pointC.y - pointP.y
            const dd = Math.sqrt(dx * dx + dy * dy)
            const a = Math.asin(radius / dd)
            const b = Math.atan2(dy, dx)

            let t = b - a
            const point1 = {x: radius * Math.sin(t) + pointC.x, y: radius * -Math.cos(t) + pointC.y}

            t = b + a
            const point2 = {x: radius * -Math.sin(t) + pointC.x, y: radius * Math.cos(t) + pointC.y}

            const chord: number = Utils.GetDistanceBetween2Point(point1, point2)
            const fiO: number = Math.atan((chord / 2) / radius)

            return {
                point1,
                point2,
                fi: fiO
            }
        },
    GetNewPointComplex: (point: Point, l: number, fi: number): Point => {
        const dX: number = l * Math.sin(fi)
        const dY: number = l * Math.cos(fi)
        return {
            x: point.x + dX,
            y: point.y + dY
        }
    },

    LinesHasIntersection: (line1: Line, line2: Line) => {
        // if the lines intersect, the result contains the x and y of the intersection (treating the lines as infinite) and booleans for whether line segment 1 or line segment 2 contain the point
        let denominator, a, b, numerator1, numerator2, result = {
            x: null,
            y: null,
            onLine1: false,
            onLine2: false
        }
        denominator = ((line2.point2.y - line2.point1.y) * (line1.point2.x - line1.point1.x)) - ((line2.point2.x - line2.point1.x) * (line1.point2.y - line1.point1.y))
        if (denominator == 0) {
            return result
        }
        a = line1.point1.y - line2.point1.y
        b = line1.point1.x - line2.point1.x
        numerator1 = ((line2.point2.x - line2.point1.x) * a) - ((line2.point2.y - line2.point1.y) * b)
        numerator2 = ((line1.point2.x - line1.point1.x) * a) - ((line1.point2.y - line1.point1.y) * b)
        a = numerator1 / denominator
        b = numerator2 / denominator
        // if we cast these lines infinitely in both directions, they intersect here:
        result.x = line1.point1.x + (a * (line1.point2.x - line1.point1.x))
        result.y = line1.point1.y + (a * (line1.point2.y - line1.point1.y))
        /*
                // it is worth noting that this should be the same as:
                x = line2.point1.x + (b * (line2.point2.x - line2.point1.x));
                y = line2.point1.x + (b * (line2.point2.y - line2.point1.y));
                */
        // if line1 is a segment and line2 is infinite, they intersect if:
        if (a > 0 && a < 1) {
            result.onLine1 = true
        }
        // if line2 is a segment and line1 is infinite, they intersect if:
        if (b > 0 && b < 1) {
            result.onLine2 = true
        }
        // if line1 and line2 are segments, they intersect if both of the above are true
        return result.onLine1 && result.onLine2
    },
    Rx: (r0: number, h0: number, height: number): number => {
        return (r0 * (h0 - height)) / h0
    },
    Rcx: (r0: number, h0: number, hc: number, height: number): number => {
        return (r0 * (hc - height)) / hc
    }
}

export default Utils
