import * as THREE from 'three'

import {OrbitControls} from 'three/examples/jsm/controls/OrbitControls'

/**
 *This is Main canvas we need to work with this
 */
class App3d {
    scene: THREE.Scene
    camera: THREE.PerspectiveCamera
    renderer: THREE.WebGLRenderer
    controls: OrbitControls
    plane: any
    helper: any

    constructor() {
        let container: HTMLElement = document.getElementById('3d')

        container = document.getElementById('3d')
        document.body.appendChild(container)

        this.scene = new THREE.Scene()
        this.camera = new THREE.PerspectiveCamera(
            100,
            window.innerWidth / window.innerHeight,
            window.innerWidth / window.innerHeight,
            10000
        )
        this.camera.position.set(0, 100, 0)
        this.renderer = new THREE.WebGLRenderer({antialias: true})
        this.renderer.setSize(800, 400)

        container.appendChild(this.renderer.domElement)

        this.renderer.domElement.id = 'myCanvas'
        this.renderer.domElement.className = 'border_red'


        this.controls = new OrbitControls(this.camera, this.renderer.domElement)
        this.controls.update()

        let light: THREE.DirectionalLight
        light = new THREE.DirectionalLight(0xf542c5, 1)
        light.position.setScalar(1)
        this.scene.add(light)
        this.scene.add(new THREE.AmbientLight(0x47ad24, 0.5))

        const plane = new THREE.Plane(new THREE.Vector3(0, 1, 0), -5)
        this.helper = new THREE.PlaneHelper(plane, 2000)
        this.helper.material = new THREE.MeshMatcapMaterial({color: 0xffb700})
        this.helper.rotateY(Math.PI / 2)
        this.scene.add(this.helper)

        //

        const grid: THREE.GridHelper = new THREE.GridHelper(
            2000,
            200,
            0xffffff,
            0xffffff
        );

        (<THREE.Material>grid.material).opacity = 2;
        (<THREE.Material>grid.material).transparent = true

        this.scene.add(grid)

        const grid1 = new THREE.GridHelper(2000, 2000, 0xc41d1d, 0xc41d1d);
        (<THREE.Material>grid.material).opacity = 0.6;
        (<THREE.Material>grid.material).transparent = true
        this.scene.add(grid1)

        document.addEventListener('keyup', (event) => {
            if (event.code === 'Space') {
                console.log('Space pressed')
                this.camera.position.set(0, 100, 0)
                this.controls.update()
                this.camera.lookAt(0, 0, 0)
            }
        })

        this.renderer.setPixelRatio(3)
        this.animate()
    }

    /**
     * Constructor create canvas no need parameter
     */

    animate() {
        requestAnimationFrame(this.animate.bind(this))
        this.renderer.render(this.scene, this.camera)
    }

    addCone(x: number, z: number, r: number, h: number): void {
        const coneGeometry = new THREE.CylinderGeometry(0, r, h, 100)
        const material = new THREE.LineBasicMaterial({color: 0x1a27e8})
        const cone = new THREE.Mesh(coneGeometry, material)
        this.scene.add(cone)
        cone.position.set(x, h / 2, z)
    }

    Setheight(h) {
        this.helper.plane.constant = -h
    }
}

export default App3d
